import { company_data } from '../company_data'
import React from 'react'
import { Button, Card, Grid, Link } from '@material-ui/core'
import { GetData } from '../extractor/csvParse';
import CachedIcon from '@material-ui/icons/Cached';


function Random() {

    var randomProperty = function () {
        var keys = Object.keys(company_data.companies);
        return company_data['companies'][keys[keys.length * Math.random() << 0]]["file"];
    };
    var randomQues = (question) => {
        return question[question.length * Math.random() << 0]
    }
    var gen_ques = async () => {
        var file_name = randomProperty()
        var questionArr = await GetData(file_name)
        var random_ques_arr = randomQues(questionArr)

        return random_ques_arr
    }

    const [ques, setQues] = React.useState([])
    const [loading, setLoading] = React.useState(false);
    const handleClinkLink = (e) => {
        e.preventDefault();
        window.open(e.target.href, "_blank")
    }
    const handleNewQues = async () => {
        setLoading(true)
        const res = await gen_ques();
        setQues(res)
        setLoading(false);
    }
    const ButtonSearch = ({ text }) => {
        return <><Button variant="outlined" color="primary" onClick={handleNewQues} >{text}</Button></>
    }

    return (
        <>
            <Card variant="outlined" style={{ padding: ".9em" }}>
                {
                    loading ?
                        <>
                            <div style={{ display: "flex", justifyContent: "center" }}>
                                <CachedIcon />
                            </div>
                        </> : ques.length === 6 ?
                        <>
                            <Grid container alignItems="center" justify="center">
                                <Grid item xs={2} align="center">
                                    {ques[0]}
                                </Grid>
                                <Grid item xs={2} align="center">
                                    {ques[1]}
                                </Grid>
                                <Grid item xs={2} align="center">
                                    <Link href={`${ques[5]}`} onClick={handleClinkLink}>
                                        Problem Link
                                    </Link>
                                </Grid>
                                <Grid item xs={2} align="center">
                                    {ques[3]}
                                </Grid>
                                <Grid item xs={4} align="center">
                                    <ButtonSearch text={"new question"} />
                                </Grid>
                            </Grid>
                        </> :
                        <>
                            <div style={{display:"flex",justifyContent:"center"}}>
                                <ButtonSearch text={"Practice Random Question"} />
                            </div>
                        </>
                }
            </Card>
        </>
    )
}

export default Random
